    judgeColor(x,y){//Â∫ßÊ®ô„ÇíÂÖÉ„Å´‰ΩïÁÇπ„Å†„Å£„Åü„Åã„ÇíËøî„ÅôÈñ¢Êï∞
        //„Åì„ÅÆÈñ¢Êï∞„ÅØÁÇπÊï∞„ÇíËøî„Åô„Åü„ÇÅ„ÄÅ„Åù„Çå„Åå‰ΩïËâ≤„Å™„ÅÆ„Åã„ÅØÈñ¢Êï∞Âëº„Å≥Âá∫„Åó„ÇÇ„Å®„ÅßÂá¶ÁêÜ„Åó„Å¶„ÇÇ„Çâ„ÅÜ
        const C_x = 499;
        const C_y = 447;

        let distance = 0;
        //Ë∑ùÈõ¢„ÇíË®àÁÆó„ÄÄ‰∫å‰πóÔºã2‰πó„ÄÄ„ÅÆ„É´„Éº„Éà
        distance = ((C_x - x) * (C_x - x)) + ((C_y - y) * (C_y - y));
        //„É´„Éº„Éà„ÇíÂèñ„Çã
        distance = Math.sqrt(distance);
        //Êï¥Êï∞„Å´‰∏∏„ÇÅ„Çã
        distance = Math.ceil(distance);

        if(distance < 48){//ÈªÑËâ≤„ÅÆ„Å®„Åì„Çç
            return 10;
        }else if(48<= distance && distance < 92){//Ëµ§„ÅÆ„Å®„Åì„Çç
            return 9;
        }else if(92<= distance && distance < 155){//Èùí
            return 7;
        }else if(155<= distance && distance < 201){//Èªí„ÅÆ„Å®„Åì„Çç
            return 5;
        }else if(201<= distance && distance < 248){//ÁôΩ„ÅÆ„Å®„Åì„Çç
            return 3;
        }else if(248 <= distance){//Á∑ë
            return 0;
        }
    }

